import Vue from 'vue'
import Router from 'vue-router'
//import Login from '.././views/login.vue'
//import NotFound from '.././views/404.vue'
//import Home from '.././views/index.vue'
Vue.use(Router)

export default new Router({
  mode: 'history',//去掉#号
  routes: [
    {
      path: '/',
      name: 'Login',
      component: resolve => require(['../pages/login'],resolve),
      meta: {title: '登录'},
    },
    {
      path: '/password',
      name: 'Password',
      component: resolve => require(['../pages/password'],resolve),
      meta: {title: '找回密码'},
    },
    {
      path: '/personal',
      name: 'Personal',
      component: resolve => require(['../pages/personal/personal'],resolve),
      meta: {title: '个人信息'},
    },
    {
      path: '/wallet',
      name: 'Wallet',
      component: resolve => require(['../pages/personal/wallet'],resolve),
      meta: {title: '个人钱包'},
    },



    {
      path: '/cash',
      name: 'Cash',
      component: resolve => require(['../pages/personal/cash'],resolve),
      meta: {title: '个人钱包-提现'},
    },
    {
      path: '/customer',
      name: 'Customer',
      component: resolve => require(['../pages/customer/customer'],resolve),
      meta: {title: '联系客服'},
    },



    {
      path: '/address',
      name: 'Address',
      component: resolve => require(['../pages/address/address'],resolve),
      meta: {title: '我的地址'},
    },


    {
      path: '/cart',
      name: 'Cart',
      component: resolve => require(['../pages/cart/cart'],resolve),
      meta: {title: '购物车'},
    },


    {
      path: '/confirm',
      name: 'Confirm',
      component: resolve => require(['../pages/cart/confirmOrder'],resolve),
      meta: {title: '订单确认'},
    },


    {
      path: '/deal',
      name: 'Deal',
      component: resolve => require(['../pages/deal/deal'],resolve),
      meta: {title: '交易发布'},
    },
    {
      path: '/dealPublish',
      name: 'DealPublish',
      component: resolve => require(['../pages/deal/dealPublish'],resolve),
      meta: {title: '交易发布'},
    },



    {
      path: '/order',
      name: 'Order',
      component: resolve => require(['../pages/order/order'],resolve),
      meta: {title: '订单管理'},
    },
    {
      path: '/orderStore',
      name: 'OrderStore',
      component: resolve => require(['../pages/order/orderStore'],resolve),
      meta: {title: '店铺订单'},
    },


    {
      path: '/goods',
      name: 'Goods',
      component: resolve => require(['../pages/goods/goods'],resolve),
      meta: {title: '我的商品'},
    },
    {
      path: '/addGoods',
      name: 'AddGoods',
      component: resolve => require(['../pages/goods/addGoods'],resolve),
      meta: {title: '新增商品'},
    },


    {
      path: '/query',
      name: 'Query',
      component: resolve => require(['../pages/query/query'],resolve),
      meta: {title: '分类查询'},
    },
    {
      path: '/queryDetail',
      name: 'QueryDetail',
      component: resolve => require(['../pages/query/queryDetail'],resolve),
      meta: {title: '分类查询'},
    },


  ]
})

